// components/TrackedButton.jsx

import { sendMetrikaEvent } from "../../hooks/useMetrica";

const TrackedButton = ({ 
  children, 
  eventName, 
  eventParams = {}, 
  onClick, 
  component = 'button',
  href,
  ...props 
}) => {
  const handleClick = (e) => {
    //    
    if (eventName) {
      sendMetrikaEvent(eventName, eventParams);
    }
    
    //   onClick  
    if (onClick) {
      onClick(e);
    }
  };

  //  component = 'a',  
  if (component === 'a') {
    return (
      <a 
        href={href} 
        onClick={handleClick} 
        {...props}
      >
        {children}
      </a>
    );
  }

  //    
  return (
    <button onClick={handleClick} {...props}>
      {children}
    </button>
  );
};

export default TrackedButton;